<template>
    <div class="message-dialog">
      <h2>Boîte de dialogue</h2>
      <div class="message-history">
        <div v-for="(message, index) in messages" :key="index" class="message">
          <span class="sender">{{ message.sender }}:</span> {{ message.text }}
        </div>
      </div>
      <div class="message-input">
        <input type="text" v-model="newMessage" @keyup.enter="sendMessage" placeholder="Écrire un message...">
        <button @click="sendMessage">Envoyer</button>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        messages: [
          { sender: 'Utilisateur 1', text: 'Bonjour Utilisateur 2!' },
          { sender: 'Utilisateur 2', text: 'Bonjour Utilisateur 1, comment vas-tu?' }
        ],
        newMessage: ''
      };
    },
    methods: {
      sendMessage() {
        if (this.newMessage.trim() !== '') {
          this.messages.push({ sender: 'Utilisateur 1', text: this.newMessage });
          this.newMessage = '';
          // Vous pouvez envoyer ce message à votre backend ici si nécessaire
        }
      }
    }
  };
  </script>
  
  <style scoped>
  .message-dialog {
    max-width: 400px;
