<template>
  <nav class="navigation">
    <!-- Bouton "Plus" pour les éléments de navigation -->
    <button class="menu-btn" @click="toggleMenu">Plus</button>

    <!-- Liste des éléments de navigation -->
    <ul :class="{ 'show-menu': isMenuOpen } ">
      <li>
        <router-link to="/user/analytics" class="nav-link" active-class="active">Dashboard</router-link>
      </li>
      <li>
        <router-link to="/user/suivi" class="nav-link" active-class="active">Objectif</router-link>
      </li>
      <li>
        <router-link to="/user/rapport" class="nav-link" active-class="active">Rapport </router-link>
      </li>
    </ul>
    <li @click="istoggleDropdown" class="nav">
     Programme
    </li>
    <div v-if="showDropdownOpen" class="dropdownAC">
      <ul>
        <router-link to=""><li>Programme de la semaine</li></router-link>
        <router-link to="/user/ActiviteHebdomadaire"><li>Activites hedomadaire</li></router-link>
      </ul>
    </div>
    <li @click="toggleDropdown" class="nav">CDE</li>

    <div v-if="showDropdown" class="dropdownCDE">
      <!-- Your dropdown content goes here -->
      <ul>
        <router-link to="/user/inscription"><li>Inscription</li></router-link> 
        <router-link to="/user/listOfchildren"><li>list de presence</li></router-link> 
        <router-link to="/user/Absent"><li>Absents</li></router-link> 
        <!-- Add other dropdown items here -->
      </ul>
    </div>

    <div class="search">
      <input type="text" v-model="searchQuery" @input="search" placeholder="Rechercher...">
      <ul v-if="searchResults.length > 0" class="dropdown">
        <li v-for="(result, index) in searchResults" :key="index" @click="displayResult(result)">{{ result }}</li>
      </ul>
      <p v-if="searchQuery.length > 0 && searchResults.length === 0" class="dropdown">Aucun résultat trouvé.</p>
    </div>
    <div class="icons">
      <img src="@/assets/icons/new-message-icon.svg"  alt="Notification" class="notification-icon" @click="toggleNotificationDropdown">
      <div v-if="isNotificationDropdownOpen" class="dropdown">
        <ul>
          <li v-if="!hasNotifications">Aucune notification</li>
        </ul>
      </div>
      <img :src="profilePhotoURL" @click="toggleProfileDropdown" alt="" class="profile-icon">
      <div v-if="isProfileDropdownOpen" class="dropdown">
        <ul>
          <router-link to="/user/userInfo"><li>Éditer photo</li></router-link> 
          <router-link to="/user/changeCount"><li>Changer de compte</li></router-link> 
          <router-link to="/user/gererCount"><li>Gerer le compte</li></router-link> 
          <router-link to="/user/aide"><li>Aide</li></router-link> 
          <router-link to="/user/Parametre"><li>Paramètres</li></router-link> 
          <router-link to="/user/logout"><li>Déconnexion</li></router-link> 
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
import  axios  from 'axios';
// import defaultProfileIcon from '@/assets/images/profile-icon.svg';
export default {
  data() {
    return {
      showDropdown:false,
      showDropdownOpen:false,
      isMenuOpen: false,
      searchQuery: '',
      searchResults: [],
      isNotificationDropdownOpen: false,
      isProfileDropdownOpen: false,
      hasNotifications: false,
      profilePhotoURL: ''
    };
  },
  mounted() {
    this.fetchProfilePicture();
    // this.profilePhotoURL = localStorage.getItem('profilePhotoURL') || defaultProfileIcon;
    // console.log('image:',this.profilePhotoURL);
    // if(!this.profilePhotoURL){
    //   this.profilePhotoURL= defaultProfileIcon
    // }
    // console.log('image:',this.profilePhotoURL);
  },
  beforeMount() {
    document.removeEventListener('click',this.handleClickOutside);
  },
  methods: {
    toggleDropdown(){
      this.showDropdown = !this.showDropdown;
    },
    istoggleDropdown(){
      this.showDropdownOpen = ! this.showDropdownOpen;
    },
    toggleMenu() {
      this.isMenuOpen = !this.isMenuOpen;
    },
    toggleNotificationDropdown() {
      this.isNotificationDropdownOpen = !this.isNotificationDropdownOpen;
    },
    toggleProfileDropdown() {
      this.isProfileDropdownOpen = !this.isProfileDropdownOpen;
    },

    fetchProfilePicture() {
      const token = localStorage.getItem('token');
      const headers = {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      };
      
      axios.get('http://localhost:8000/admin/getprofil', { headers })
        .then(res => {
          this.profilePhotoURL = res.data.profilePhotoUrl; // Assuming the response contains the profile photo URL
          console.log('profilePicture',this.profilePhotoURL);
        })
        .catch(error => {
          console.error('Error fetching profile picture:', error);
          // Handle errors here
        });
    },
   
    search() {
      this.searchResults=[];
      //verifier si la requette de rechercher est vide
      if(this.searchQuery.length ===0){
        return
      }
      // Effectuer une requête à la base de données pour récupérer les résultats de recherche
      // Remplacer cet exemple par votre propre requête à la base de données
       axios.get('http://votre-api.com/recherche?q=' + this.searchQuery)
      //   .then(response => {
      //     this.searchResults = response.data;
      //   })
      //   .catch(error => {
      //     console.error('Error fetching search results:', error);
      //   });
      
      // Exemple: données de recherche simulées
      this.searchResults = ['Résultat 1', 'Résultat 2', 'Résultat 3', 'Résultat 4'];
    },
    displayResult(result) {
      // Afficher les informations sur le résultat sélectionné
      console.log('Informations sur le résultat sélectionné:', result);
      // Vous pouvez également naviguer vers une autre page ou afficher les détails dans un composant modal, etc.
    }
  }
}
</script>


