<template>
    <div class="page">
      <div><br>
        <h5>Liste des enfants absents</h5>
        <em>Effectif total: ({{ children.length }})</em>
      </div><br>
      <div v-for="child in children" :key="child._id" @click="toggleDropdown(child._id)">
        <div class="profile-info" @click="preventDropdownClose($event)">
          <img src="../assets/icons/profile-icon.svg" alt="Profile Icon" class="profile-icon">
          <p>{{ child.firstName }}</p>
          <p>class: ({{ child.class }})</p>
          <p>tel: ({{ child.phoneNumber }})</p>
          <p class="nbre">Nbre de fois:({{ child.absentCount }})</p>
          <p class="date">Date:({{ child.absentDates }})</p>
        </div><br>
      </div>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  import defaultProfileIcon from '@/assets/icons/profile-icon.svg';
  
  export default {
    data() {
      return {
        children: [],
        profileImageURL: '',
      };
    },
    created() {
      this.fetchChildren();
    },
    mounted() {
      document.addEventListener('click', this.handleClickOutside);
      this.profilePhotoURL = localStorage.getItem('profilePhotoURL');
      if (!this.profilePhotoURL) {
        this.profilePhotoURL = defaultProfileIcon
      }
    },
    methods: {
     
      async fetchChildren() {
        // const token = localStorage.getItem('token');
        // const headers = {
        //   'Content-Type': 'application/json',
        //   'Authorization': `Bearer ${token}`
        // };
        try {
          const response = await axios.get('http://localhost:8000/absent/absent',);
          console.log('data',response.data);
          this.children = response.data.sort((a, b) => {
            const nameA = a.firstName.toUpperCase();
            const nameB = b.firstName.toUpperCase();
            if (nameA < nameB) {
              return -1;
            }
            if (nameA > nameB) {
              return 1;
            }
            return 0;
          });
        } catch (error) {
          console.error('Erreur lors de la récupération des enfants :', error);
        }
      },
    }
  };
  </script>
  
  
